<template>
  <div class="home">
    <div class="home-top">
      <div class="home-top-all">
        <div class="home-top-r-list">
          <div
            v-for="(item, index) in list"
            :key="index"
            :class="i == index ? 'll' : ''"
            class="list-item"
          >
            <router-link
              :to="
                index == 1
                  ? { path: '/newsList', query: { id: pId } }
                  : item.routerName
              "
              :class="i == index ? 'cl' : ''"
            >
              <!-- <router-link :to="item.routerName" exact-active-class="cl"> -->
              <img :src="i == index ? item.img : item.img1" alt="" />
              <div>{{ item.title }}</div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  name: "homeTop",
  props: ["i"],
  data() {
    return {
      list: [
        {
          id: 1,
          title: "",
          img: require("../assets/one-hover.png"),
          img1: require("../assets/one.png"),
          routerName: { name: "newIndex" },
        },
        {
          id: 2,
          title: "",
          img: require("../assets/ten-hover.png"),
          img1: require("../assets/ten.png"),
          routerName: { name: "newsList" },
        },
        {
          id: 3,
          title: "",
          img: require("../assets/two-hover.png"),
          img1: require("../assets/two.png"),
          routerName: { name: "zActivity" },
        },
        {
          id: 4,
          title: "",
          img: require("../assets/four-hover.png"),
          img1: require("../assets/four.png"),
          routerName: { name: "Procedures" },
        },
        {
          id: 5,
          title: "",
          img: require("../assets/five-hover.png"),
          img1: require("../assets/five.png"),
          routerName: { name: "Mine" },
        },
      ],
      pId: "",
    };
  },
  methods: {
    kh() {
      let that = this;
      const params = {
        type: "mobile",
      };
      console.log(params);
      that.$axios
        .post("/f/ajaxHome/ajax_categoryMapList", qs.stringify(params))
        .then((res) => {
          console.log(res, "");
          if (res.data.state == 1) {
            if (res.data.data && res.data.data.length != 0) {
              if (res.data.data[0].module == "article") {
                that.pId = res.data.data[0].id;
              }
            }
          } else {
          }
        })
        .catch((err) => {
          console.log("");
        });
    },
  },
  created() {
    console.log(JSON.parse(localStorage.getItem("LIST4")), 899);
    if (JSON.parse(localStorage.getItem("LIST4"))) {
      this.list = JSON.parse(localStorage.getItem("LIST4"));
    }
    // urlidpc
    this.kh();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.home-top {
  width: 7.5rem;
  height: 1.66rem;
  /* background-color: rgba(255, 255, 255, 0.96); */
  background-color: rgba(255, 255, 255);
  box-shadow: 0rem -0.02rem 0.02rem 0rem rgba(0, 0, 0, 0.07);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  padding: 0.14rem 0.52rem;
  box-sizing: border-box;
}
.home-top-all {
  width: 100%;
  height: 1.66rem;
}

.home-top-r-list {
  width: 100%;
  height: 1.66rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.list-item {
  height: 100%;
}
a {
  display: inline-block;
  font-family: PingFangSC-Medium;
  font-size: 0.2rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #9fa4b3;
  text-decoration: none;
  height: 100%;
}
a img {
  width: 0.48rem;
  height: 0.48rem;
}
.cl div {
  font-family: PingFangSC-Regular;
  font-size: 0.2rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #4760d7;
}
.ll {
  height: 100%;
}
</style>


