<template>
  <div class="publicTop" @click="go">
    <img src="../../static/yiyiyi.png" alt="" />
  </div>
</template>

<script>
export default {
  name: "publicTop",
  data() {
    return {};
  },
  methods: {
    go() {
      if (this.$route.path == "/") {
      } else {
        this.$router.push({
          path: "/",
        });
      }
    },
  },
  created() {
    console.log(this.$route.path, 8888888);
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.publicTop {
  width: 7.5rem;
  height: 1.2rem;
  background-image: linear-gradient(90deg, #5993ff 0%, #5c42de 100%);
  display: flex;
  align-items: center;
  justify-content: center;
}
.publicTop img {
  width: 4.24rem;
  /* height: 0.68rem; */
}
</style>


