<script>
// import util from "../utils/ut.js";
// var util = require("../utils/ut.js");
import qs from "qs";
var dat = require("../utils/date.js");
export default {
  name: "Calendar",
  data() {
    return {
      currentYear: new Date().getFullYear(),
      currentMonth: new Date().getMonth() + 1,
      nowYear: new Date().getFullYear(),
      nowMonth: new Date().getMonth() + 1,
      nowDate: new Date().getDate(),
      currentMonthDateLen: 0, // 
      preMonthDateLen: 0, // 
      allArr: [],
      nowData: "",
      list_render: [],
      num_arr: [],
      dataList: [],
      check_date: "",
      ll: new Date().getFullYear(),
      pp: new Date().getMonth() + 1,
      num: 0,
    };
  },
  watch: {
    dataList: {
      handler(newV, oldV) {
        console.log(newV, oldV, "");
      },
    },
  },
  methods: {
    getNowData(month, date) {
      console.log(month, date);
      this.dataList = [];
      var data = date;
      var month = month;
      console.log(this.num_arr);
      var num_arr = this.num_arr;
      var list_render = this.list_render;
      var isOk = 0;
      for (var j = 0; j < num_arr.length; j++) {
        if (Number(num_arr[j].ri) == data && month == "current") {
          isOk = 1;
        }
      }
      console.log(isOk);
      if (isOk == 1) {
        for (var i = 0; i < list_render.length; i++) {
          var str = list_render[i].startTimeFormat;
          var str2 = str.substring(str.length - 2);
          if (str2 == data) {
            this.dataList.push(list_render[i]);
          }
        }
        this.dataList = this.dataList;
        console.log(this.dataList, "this.dataList");
        this.check_date = data;
        this.$refs.box.scrollTop = 0;
      } else {
        this.dataList = [];
        this.check_date = "";
      }
    },
    getTogg(typename, id) {
      console.log(typename, id);
      // this.$store.commit("updataDetailId", id);
      // id
      if (typename == "" || typename == "") {
        // this.$router.push({ name: "xuanDetail" });
        this.$router.push({
          path: "/recruitmentFairForm",
          query: { type: 3, id: id },
        });
      } else if (typename == "" || typename == "") {
        // this.$router.push({ name: "shuangDetail" });
        this.$router.push({
          path: "/bilateralchosefairForm",
          query: { type: 4, id: id },
        });
      } else if (typename == "") {
        // this.$router.push({ name: "activityDetail" });
        this.$router.push({
          path: "/activityDetail",
          query: { id: id },
        });
      } else {
      }
    },
    // 
    getDateLen(year, month) {
      let actualMonth = month - 1;
      let timeDistance = +new Date(year, month) - +new Date(year, actualMonth);
      return timeDistance / (1000 * 60 * 60 * 24);
    },
    // 1
    getFirstDateWeek(year, month) {
      return new Date(year, month - 1, 1).getDay();
    },
    //  
    preMonth(year, month) {
      if (month == 1) {
        return {
          year: --year,
          month: 12,
        };
      } else {
        return {
          year: year,
          month: --month,
        };
      }
    },
    //  
    nextMonth(year, month) {
      if (month == 12) {
        return {
          year: ++year,
          month: 1,
        };
      } else {
        return {
          year: year,
          month: ++month,
        };
      }
    },
    // 
    getCurrentArr() {
      let currentMonthDateLen = this.getDateLen(
        this.currentYear,
        this.currentMonth
      ); // 
      let currentMonthDateArr = []; // 
      if (currentMonthDateLen > 0) {
        for (let i = 1; i <= currentMonthDateLen; i++) {
          currentMonthDateArr.push({
            month: "current", // 
            date: i,
          });
        }
      }
      this.currentMonthDateLen = currentMonthDateLen;
      return currentMonthDateArr;
    },
    // 
    getPreArr() {
      let preMonthDateLen = this.getFirstDateWeek(
        this.currentYear,
        this.currentMonth
      ); // 1 == 
      let preMonthDateArr = []; // 
      if (preMonthDateLen > 0) {
        let { year, month } = this.preMonth(
          this.currentYear,
          this.currentMonth
        ); //  
        let date = this.getDateLen(year, month); // 
        for (let i = 0; i < preMonthDateLen; i++) {
          preMonthDateArr.unshift({
            // 
            month: "pre", // 
            date: date,
          });
          date--;
        }
      }
      this.preMonthDateLen = preMonthDateLen;
      return preMonthDateArr;
    },
    // 
    getNextArr() {
      let nextMonthDateLen =
        35 - this.preMonthDateLen - this.currentMonthDateLen; // 
      let nextMonthDateArr = []; // 
      if (nextMonthDateLen > 0) {
        for (let i = 1; i <= nextMonthDateLen; i++) {
          nextMonthDateArr.push({
            month: "next", // 
            date: i,
          });
        }
      }
      return nextMonthDateArr;
    },
    // 
    getAllArr() {
      let preArr = this.getPreArr();
      let currentArr = this.getCurrentArr();
      let nextArr = this.getNextArr();
      // console.log(preArr,currentArr,nextArr)
      let allArr = [...preArr, ...currentArr, ...nextArr];
      this.allArr = allArr;
      let sendObj = {
        currentYear: this.currentYear,
        currentMonth: this.currentMonth,
        nowYear: this.nowYear,
        nowMonth: this.nowMonth,
        nowDate: this.nowDate,
        allArr: allArr,
      };
      console.log(this.allArr, "arr");
      console.log(sendObj);
      // this.triggerEvent('sendObj', sendObj)
    },
    //  
    gotoPreMonth() {
      let { year, month } = this.preMonth(this.currentYear, this.currentMonth);
      this.currentYear = year;
      this.currentMonth = month;
      console.log(
        this.currentYear,
        "this.currentYear",
        this.ll,
        this.pp,
        this.currentYear == this.ll,
        this.currentMonth == this.pp,
        this.currentYear == this.ll && this.currentMonth == this.pp
      );
      console.log(this.currentMonth, "this.currentMonth");
      this.num = 0;
      this.list_render = [];
      this.allArr = [];
      this.num_arr = [];
      this.check_date = "";
      this.dataList = [];
      this.getAllArr();
      this.getCalendar();
      console.log(this.allArr, 79);
    },
    //  
    gotoNextMonth() {
      let { year, month } = this.nextMonth(this.currentYear, this.currentMonth);
      this.currentYear = year;
      this.currentMonth = month;
      this.num = 0;
      this.list_render = [];
      this.allArr = [];
      this.num_arr = [];
      this.check_date = "";
      this.dataList = [];
      this.getAllArr();
      this.getCalendar();
    },
    getCalendar() {
      const params = {
        time: this.currentYear + "-" + this.currentMonth,
      };
      console.log(params);
      this.$axios
        // .get(
        //   "/f/recruitmentFair/mobileCalendar?time=" +
        //     this.currentYear +
        //     "-" +
        //     this.currentMonth
        // )
        .post("/f/recruitmentFair/mobileCalendar", qs.stringify(params))
        .then((res) => {
          console.log(res, "");
          if (res.data.state == 1) {
            if (res.data.object && res.data.object.length != 0) {
              this.list_render = res.data.object;
              var arr = res.data.object;
              var allArr = this.allArr;
              var allArr9 = this.allArr;
              var num_arr = this.num_arr;
              var str = "";
              var data = new Date().getMonth() + 1;
              for (var i = 0; i < arr.length; i++) {
                str = arr[i].startTimeFormat; //2
                var obj = {
                  nian: str.slice(0, 4),
                  yue: str.slice(5, 7),
                  ri: str.slice(8, 10),
                };
                num_arr.push(obj);
              }
              this.num_arr = num_arr;
              console.log(allArr);
              console.log(num_arr);
              for (var j = 0; j < num_arr.length; j++) {
                for (var a = 0; a < allArr.length; a++) {
                  if (
                    Number(num_arr[j].ri) == allArr[a].date &&
                    allArr[a].month == "current"
                  ) {
                    console.log(allArr[a].date);
                    allArr[a].hav = 1;
                  }
                }
              }
              this.allArr = allArr;
              console.log(this.allArr, "this.allArr");
              this.$set(this.allArr, this.allArr);
              //
              for (var i = 0; i < allArr.length; i++) {
                console.log(
                  new Date().getDate() == allArr[i].date &&
                    allArr[i].month == "current",
                  88
                );
                if (
                  new Date().getDate() == allArr[i].date &&
                  allArr[i].month == "current"
                ) {
                  this.dataList = [];
                  var isOk4 = 0;
                  for (var w = 0; w < num_arr.length; w++) {
                    console.log(num_arr[w], "w");

                    if (
                      Number(num_arr[w].yue) == this.nowMonth &&
                      Number(num_arr[w].ri) == this.nowDate
                    ) {
                      isOk4 = 1;
                    }
                  }

                  if (isOk4 == 1) {
                    for (var r = 0; r < this.list_render.length; r++) {
                      var str10 = this.list_render[r].startTimeFormat;
                      var str20 = str10.substring(str10.length - 2);
                      console.log(str10, str20);

                      if (str20 == this.nowDate) {
                        this.dataList.push(this.list_render[r]);
                      }
                    }
                    this.check_date = this.nowDate;
                  }
                }
              }
              this.dataList = this.dataList;
              console.log(this.dataList, "this.dataList", 444);
            }
          } else {
          }
        })
        .catch((err) => {
          console.log("");
        });
    },
  },
  created() {
    this.getAllArr();
    console.log(this.allArr);
    console.log(new Date().getDate());
    var allArr = this.allArr;
    for (var i = 0; i < allArr.length; i++) {
      if (new Date().getDate() == allArr[i].date) {
        console.log(i);
        if (i < 7) {
          this.num = 0;
        } else if (i > 6 && i < 14) {
          this.num = 1;
        } else if (i > 13 && i < 21) {
          this.num = 2;
        } else if (i > 20 && i < 28) {
          this.num = 3;
        } else if (i > 27 && i < 35) {
          this.num = 4;
        } else if (i > 34 && i < 42) {
          this.num = 5;
        }
      }
    }
    this.getCalendar();
  },
  mounted() {},
  beforeDestory() {},
  destroyed() {},
};
</script>
